.Header {
    color:white;
    transition: 400ms ease-in-out 0s;
    font-size: 35px;
    padding-top:50px;
    font-family: 'ITC Avant Garde Gothic Std Book';
    padding-bottom:100px;
}
.skillsContainer {
    width: 70%;
    transition: 400ms ease-in-out 200ms;
    font-family: 'ITC Avant Garde Gothic Std Medium';
    font-size: 18px;
    margin-left: auto;
    text-align: left;
    margin-right: auto;
    display: flex;
    justify-content: space-around;
    padding-bottom:150px;
}
.skillsContainer ul{
    list-style: none;
    padding:0;
    text-align: left;
    color:white;
    font-size: 20px;
    font-family: 'ITC Avant Garde Gothic Std XL';
}
.skillsContainer li{
    padding:3px;
    padding-left:0;      
}
.languagesHeader {
    color: white;
}
.enginesHeader {
    color:white;
}

.softwareHeader {
    color: white;
}
.separator {
    width:60%;
    height:5px;
}